const { get, controller } = sword.Decorator;
@controller('/api/mobile-evidence-center')
class mobileEvidenceCenter extends sword.Controller {
    @get('/alarminfo')
    async alarminfo() {
        try {
            const data = await sword.service('mobile-evidence-center.mobileEvidenceCenter')(this.query);
            if (data.errorcode === 200) {
                this.success(data.result);
            } else {
                throw new Error(`errorcode != 200, errorcode=${data.errorcode}`);
            }
        } catch (error) {
            sword.logger.error(error);
            const json = {
                code: 202,
                data: []
            };
            this.fail(json);
        }
    }
}
export default mobileEvidenceCenter;
